// 
// _card.scss
// 

.card {
  margin-bottom: $grid-gutter-width;
  border: 1px solid #f5f7fb;
}

.card-drop {
  color: $body-color;
}

.card-title {
  font-size: 15px;
  margin: 0 0 7px 0;
  font-weight: $font-weight-semibold;
}

.card-title-desc {
  color: $card-title-desc;
  margin-bottom: 24px;  
}

.card-body {
  padding:10px!important;
  
}

