  margin-left: 0;
  margin: 0;
}

.radio {
  padding-left: 22px;
  line-height: 26px;
  color: #404040;
  cursor: pointer;
}

.radio input[type="radio"] {
  position: absolute;
  left: -9999px;
}

.radio-btns label {
  font-size: 14px;
  color: #000;
  padding: 0px 0 0 10px;
}

.radio i {
  position: absolute;
  top: 5px;
  left: 0;
  display: block;
  width: 20px;
  height: 20px;
  outline: none;
  border: 2px solid #f16b24;
  background: #fff;
  cursor: pointer;
}

.radio i {
  border-radius: 50%;
}

.radio input + i:after {
  position: absolute;
  opacity: 0;
  transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  -ms-transition: opacity 0.1s;
  -moz-transition: opacity 0.1s;
  -webkit-transition: opacity 0.1s;
}

.radio input + i:after {
  content: '';
  top: 2px;
  left: 2px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -o-border-radius: 50%;
}

.radio input:checked + i:after {
  opacity: 1;
}

label.checkbox {
  width: 28%;
  floaT: left;
}

/*** normal state ***/
.radio i {
  transition: border-color 0.3s;
  -o-transition: border-color 0.3s;
  -ms-transition: border-color 0.3s;
  -moz-transition: border-color 0.3s;
  -webkit-transition: border-color 0.3s;
}

/*** checked state ***/
.radio input + i:after {
  content: '';
  background: url(data:image/png;base64,) no-repeat center;
  top: 2px;
  left: 2px;
  width: 9px;
  height: 9px;
