 -webkit-transform 2s,
 -webkit-filter 1.5s;
  transition: transform 2s,
 opacity 2s,
 filter 1.5s;
  transition: transform 2s,
 opacity 2s,
 filter 1.5s,
 -webkit-transform 2s,
 -webkit-filter 1.5s;
  transition-delay: .5s;
}

