  position: inherit;
}

.material .profile .toggle:before {
  width: 12px;
  height: 1px;
  margin-top: -0.5px;
  margin-left: -6px;
}

.material .profile .toggle:after {
  width: 1px;
  height: 12px;
  margin-top: -6px;
  margin-left: -0.5px;
}

