@media screen and (max-width: 990px) {
  .login_message {
    position: relative;
    top: 20px;
    left: 0;
    text-align: center;
    display: block;
  }

  .login-scrollable {
    display: block !important;
    text-align: center;
  }
  .login {
    display: block !important;
    text-align: center;
  }
}

@media screen and (max-width: 700px) {
  .leftnav {
    z-index: 99;
    min-width: 75px;
    padding: 20px;
  }
  .uk-navbar {
    z-index: 99;
  }
  .uk-navbar-nav > li > a,
  .uk-navbar-item,
  .uk-navbar-toggle {
    min-height: 60px !important;
  }
  .mob_menu_li {
    display: block;
  }
  .leftnav {
    min-width: 207px;
    position: absolute;
    left: -207px;
    top: 0;
    transition: all 0.2s ease-in-out;
  }
  .app {
    /* height: calc(100vh - 60px) !important; */
    display: block !important;
  }
  .leftnav_mobile_in {
    min-width: 207px !important;
    left: 0px !important;
  }
  .main {
    position: relative !important;
  }
  .main.over {
    position: absolute !important;
    top: 0;
    z-index: 0;
  }
  .clsBtn {
    display: block;
    text-align: right;
    position: relative;
    top: 0px;
    right: 0px;
  }
  .clsBtn button {
    border: none;
    background: none;
    color: #fff;
    padding: 0;
  }
  .leftnav .button {
    justify-content: flex-start !important;
    flex-direction: row !important;
    align-items: center !important;
  }
  .leftnav .button img {
    margin-right: 10px;
  }
  .leftnav .button {
    opacity: 1;
  }
  .btnlbl {
    font-size: 14px;
  }
}

